<template>
  <div>
    <!-- 搜索框 -->
    <van-search
      v-focus
      v-model="keyword"
      shape="round"
      background="#007bff"
      placeholder="请输入搜索关键词"
      @input="onInput"
      @search="searchFn"
    >
      <template #left>
        <van-icon name="arrow-left" size="0.48rem" color="#fff" style="margin-right: 15px" @click="$router.back()" />
      </template>
    </van-search>

    <!-- 展示搜索建议 -->
    <div class="suggest" v-if="keyword">
      <li
        v-for="(item, index) in suggestList"
        :key="index"
        v-html="highlight(keyword, item)"
        @click="searchFn(item)"
      ></li>
    </div>

    <!-- 搜索历史展示 -->
    <div class="history" v-else>
      <div class="history-header">
        <span>搜索历史</span>
        <van-icon name="delete" size="0.426667rem" @click="removeHistory" />
      </div>
      <div class="history-body">
        <span class="history-item" v-for="(item, index) in historyList" :key="index" @click="searchFn(item)">{{
          item
        }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { highlightKeyword } from '@/utils/highlight'
import { getStorage, setStorage, removeStorage } from '@/utils/storage'

export default {
  name: 'Search',
  data() {
    return {
      keyword: '',
      suggestList: [],
      timer: null,
      historyList: JSON.parse(getStorage('his')) || []
    }
  },
  methods: {
    onInput() {
      if (this.keyword.length === 0) {
        this.suggestList = []
      } else {
        // 防抖
        clearTimeout(this.timer)
        this.timer = setTimeout(async () => {
          if (this.keyword.trim()) {
            const res = await this.$API.reqSuggestList(this.keyword)
            const { options } = res.data.data
            if (options.indexOf(null) === -1) this.suggestList = options
          }
        }, 500)
      }
    },
    // 确认搜索的回调
    searchFn(value) {
      if (value.trim()) {
        // 跳转到搜索结果页
        this.$router.push(`search/${value}`)
        if (this.historyList.indexOf(value) === -1) {
          // 确保没有重复的搜索历史
          this.historyList.push(value)
          // 保存到本地存储
          setStorage('his', JSON.stringify(this.historyList))
        }
      }
    },
    // 删除搜索历史
    removeHistory() {
      removeStorage('his')
      this.historyList = []
    },
    highlight: highlightKeyword
  }
}
</script>

<style scoped lang="less">
li {
  list-style: none;
}
.suggest {
  li {
    padding: 0 15px;
    line-height: 50px;
    border-bottom: 1px solid #f8f8f8;
    font-size: 14px;
    span {
      color: #ff0000;
    }
  }
}
.history {
  padding: 0 15px;
  .history-header {
    display: flex;
    justify-content: space-between;
    padding: 12px 0;
    border-bottom: 1px solid #f8f8f8;
    font-size: 14px;
    color: #323233;
  }
  .history-body {
    font-size: 12px;
    .history-item {
      display: inline-block;
      margin: 10px 8px 0;
      padding: 8px 14px;
      border-radius: 10px;
      background-color: #efefef;
    }
  }
}
</style>
